const Gallery = props => {
  const data = props.data.allContentfulGallery
  return (
    <Layout>
      <section
        className="page-title"
        style={{ backgroundImage: `url(${Background})` }}
      >
        <div className="auto-container">
          <div className="text-center">
            <h1>Gallery</h1>
          </div>
        </div>
      </section>
      <div className="sidebar-page">
        <div className="auto-container">
          <div className="row clearfix">
            {data.edges.map(edge => {
              return (
                <div
                  className="col-md-6 col-sm-6 col-lg-4"
                  key={edge.node.image.title}
                >
                  <section className="blog-container">
                    <div className="row clearfix">
                      <div
                        className="col-lg-12 col-md-12 col-sm-12 col-xs-12 featured-blog-post wow fadeInLeft"
                        data-wow-delay="0ms"
                        data-wow-duration="1500ms"
                      >
                        <article className="inner-box">
                          <figure className="image">
                            <img
                              src={edge.node.image.file.url}
                              alt={edge.node.image.title}
                            />
                          </figure>
                        </article>
                      </div>
                    </div>
                  </section>
                </div>
              )
            })}
          </div>
        </div>
      </div>
    </Layout>
  )
}

export default Gallery
